.search {
  display: grid;
}
  .search .search_wrap {
    position: relative;
  }
  .search input {
    width: 98%;
    outline: none;

    padding: 10px 0;
    border: none;
    -webkit-align-self: center;
            align-self: center;
    margin-top: 20px;
    display: block;
    margin: 0 auto;
    font-size: 20px;
    background: #2b2c32;
    color: #989899;
    border-bottom: 1px solid #989899;
  }
  .search input:focus::-webkit-input-placeholder {
      color: transparent;
    }
  .search .cancelButton {
    position: absolute;
    font-size: 25px;
    top: 6px;
    right: 15px;
  }
  .search ul {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    list-style-type: none;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
  }
  .search ul a {
      color: #989899;
      text-decoration: none;
    }
  .search ul li {
      margin: 0 auto;
      width: 100%;
      padding-top: 15px;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
    }
  .search ul li:hover {
        background: #222328;
      }
  .search ul li img {
        max-width: 11vw;
        width: 100%;
      }
  .search ul li div {
        text-align: center;
      }

.highlight {
  background: #98989957;
}

@media (max-width: 768px) {
    .search input {
      width: 95%;
    }
    .search ul {
      grid-template-columns: repeat(2, 1fr);
    }
        .search ul li img {
          max-width: 45vw;
        }
}

@media (max-width: 465px) {
    .search input {
      width: 90%;
    }
    .search ul {
      grid-template-columns: 1fr;
    }
        .search ul li img {
          max-width: 90vw;
        }
}
